<!--
    ====================
    Toolbar(工具栏) 组件
    ====================
    TODO: 组件有待优化；
    功能：    首页右侧工具栏组件，工具栏功能的展示和编辑以及个性化工具栏内容的展示和使用；
    实现原因：每个系统中存在一些小的实用的工具，比如保费计算器，保单预览和详细信息展示等等，这些工具
            需要经常使用，所以设计布局的右侧增加工具栏，以便可以更加快捷的使用这些工具；
    实现方式： 1. 接受两个两个json数据，一个是用来记录全部存在的工具信息，另一个是用来记录当前用户选择的工具的信息
              2. 根据上述两个json进行血循环渲染出右侧菜单栏和菜单栏的增加删除操作页面并实现功能，最多可以选择七个
              3. 配合使用toolcantain组件实现组件插槽内容自定义和工具栏的内容点击切换功能；
              4. 规范的接送数据中：
                    index: 为当前工具的唯一标识,
                    icon: 当前工具的图标,
                    title: 当前组件的名称,
                    name: 当前组件需要自定义跳转的文件的名称

 -->
<template>

  <div class="picc-toolbar">
    <div class="toolPosition" />
    <div id="toolSelectList" :style="'top:'+top+';right:'+right+';'">
      <div v-for="(item,index) in hadTools" :key="index" :class="item.name" class="toolbarIconcontainer customTools" @click="clickToolOption(item.title,item.name)">
        <svg-icon :icon-class="item.icon" class="toolbarIcon" />
        <p class="had_tools_title">{{ item.title }}</p>
      </div>
      <div class="toolbarIconcontainer toolList" @click="clickToolOption('工具栏列表','toolList')">
        <svg-icon icon-class="Tools-add-nor" class="toolbarIcon" />
        <p class="operate_tools_title">操作</p>
      </div>
    </div>

    <el-drawer
      id="toolDrawer"
      :title="title"
      :append-to-body="appendToBody"
      :modal="isModal"
      :visible.sync="drawer"
      :direction="direction"
      :before-close="handleClose"
      @opened="openedCallback"
      @closed="closedCallback"
      size="360px"
      class="tool_drawer"
      ref="tooldrawer"
    >

      <slot />

      <div class="drawer_content">
        <toolList v-if="selectOption === 'toolList'" :all-data="allTools" :had-data="hadTools" @toolBarSave="toolBarSave" />

      </div>

    </el-drawer>
  </div>

</template>
<script>
import toolList from './toolList'
export default {
    name: 'Toolbar',
    components: {
        toolList
    },
    props: {
        allTools: {
            type: Array,
            default: () => []
        },
        hadTools: {
            type: Array,
            default: () => []
        },
        top: {
            type: String,
            default: () => '90px'
        },
        right: {
            type: String,
            default: () => ''
        },
        openTypePush: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            isModal: false,
            drawer: false,
            direction: 'rtl',
            appendToBody: true,
            selectOption: '',
            title: ''
        }
    },
    watch: {
        drawer(val) {
            if (this.openTypePush) {
                if (val === true) {
                    document.getElementsByClassName('toolPosition')[0].style.width = '416px'
                } else {
                    document.getElementsByClassName('toolPosition')[0].style.width = '56px'
                }
            }
        }
    },
    created() {
        for (let i = 0; i < this.allTools.length; i++) {
            for (let j = 0; j < this.allTools[i].children.length; j++) {
                const flag = this.dealDataIsHad(this.allTools[i].children[j], this.hadTools)

                if (flag) {
                    this.allTools[i].children[j]['type'] = 1
                } else {
                    if (this.hadTools.length === 7) {
                        this.allTools[i].children[j]['type'] = 2
                    } else {
                        this.allTools[i].children[j]['type'] = 0
                    }
                }
            }
        }
    },
    methods: {
        handleClose(done) {
            done()
        },
        /**
     * 判断当前的工具是否在已存在的工具栏中
     */
        dealDataIsHad(item, dataArray) {
            let flag = false
            if (dataArray.length !== 0) {
                for (let i = 0; i < dataArray.length; i++) {
                    // for (let j = 0; j < dataArray[i].children.length; j++) {
                    if (dataArray[i].index === item.index) {
                        flag = true
                    }
                    // }
                }
            }
            return flag
        },
        clickToolOption(title, name) {
            var toolsArray = document.getElementsByClassName('toolbarIconcontainer')
            for(let i=0;i<toolsArray.length;i++){
                toolsArray[i].classList.remove("active");
            }
            document.getElementsByClassName(name)[0].classList.add("active");
            this.selectOption = name
            this.title = title
            this.drawer = true
        },

        toolBarSave() {
            this.$refs.tooldrawer.closeDrawer()
            this.$emit('saveHadTools', this.hadTools)
        },
        closeToolbarContain(){
            this.$refs.tooldrawer.closeDrawer()
        },
        opendCallback(){
            document.getElementsByClassName('tool_drawer')[0].style.zIndex = '2001'
        },
        closedCallback(){
            var toolsArray = document.getElementsByClassName('toolbarIconcontainer')
            for(let i=0;i<toolsArray.length;i++){
                toolsArray[i].classList.remove("active");
            }
        }
    }
}
</script>
<style lang="scss">
#toolBar{

}
#toolSelectList{
  float:right;
  position:fixed;
}
.toolbarIconcontainer{
    width:56px;
    height:72px;
    text-align:center;
    cursor: pointer;
    & .toolbarIcon{
      margin:17px 19px 9px 19px;
    }
    & .had_tools_title{
      margin:0;
      font-size: 10px;
      color: #65677A;
      letter-spacing: 0;
      text-align: center;
      line-height: 12px;
      display: inline-block;
      width: 48px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    & .operate_tools_title{
      margin:0;
      font-size: 10px;
      color: #292B34;
      letter-spacing: 0;
      text-align: center;
      line-height: 12px;
      display: inline-block;
      width: 80%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
}
// .toolbarIconcontainer:hover{
//     // background-color: #292B34;
//     // opacity: 0.4;
//     background: red;
//     & .toolbarIcon{
//     }
// }
.customTools:hover{
  & .had_tools_title{
    font-family: MicrosoftYaHeiUI;
    font-size: 10px;
    color: #1C61FC;
    letter-spacing: 0;
    text-align: center;
    line-height: 12px;
  }
  & .toolbarIcon{
      fill:#2F377F !important;
      stroke: #2F377F !important
    }
}
// .operateTools:hover{
//   background: rgba(28,97,252,0.08);
//   & .operate_tools_title{
//     font-weight: bold;
//     font-size: 10px;
//     color: #292B34;
//     letter-spacing: 0;
//     text-align: center;
//     line-height: 12px;
//   }
// }
.toolbarIcon{
    fill:#1854DB !important;
    stroke: #1854DB !important;
}
#toolDrawer{
  margin-top: 50px;
  margin-right: 56px;
  & > .el-drawer__body{
    height:100% !important;
    overflow-y: auto;
  }
}
.tool_drawer .el-drawer__header{
  margin-bottom: 20px;
  font-weight: bold;
  font-size: 14px;
  color: #292B34;
  letter-spacing: 0;
  line-height: 16px;
}
.drawer_content{
  height: 100%;
  transition: opacity 0.28;
}
.toolPosition{
  width:56px;
  height:0;
  // transition:width 0.1s;
}
.active{
  background: rgba(28,97,252,0.08);
  font-weight: bold;
  & .operate_tools_title{
    font-weight: bold;
    font-size: 10px;
    color: #292B34;
    letter-spacing: 0;
    text-align: center;
    line-height: 12px;
  }
}

</style>
