<template>
	<div class="picc-cascade-selector">
		<el-select v-model="valueProvince" placeholder="请选择省" @change="changeProvince" ref="province">
			<el-option v-for="item in provinceList" :key="item.value" :label="item.label" :value="item.value">
			</el-option>
		</el-select>
		<el-select v-model="valueCity" placeholder="请选择市" @change="changeCity" ref="city" :disabled="isDisabled[0]">
			<el-option v-for="item in cityOptions" :key="item.value" :label="item.label" :value="item.value">
			</el-option>
		</el-select>
		<el-select v-model="valueOrigin" placeholder="请选择区" @change="changeOrigin" ref="origin" :disabled="isDisabled[1]">
			<el-option v-for="item in originOptions" :key="item.value" :label="item.label" :value="item.value">
			</el-option>
		</el-select>
	</div>
</template>
<script>
export default {
    name: "CascadeSelector",
    props: {
        data:{
            type: Array,
            default: ()=>[]
        }
    },
    data() {
        return {
            provinceList: [], // 省列表 - 省下拉框数据
            cityList: [], // 市列表
            originList: [], // 区列表
            valueProvince: "", // 所选的省
            valueCity: "", // 所选的市
            valueOrigin: "", // 所选的区
            cityOptions: [], // 市下拉框数据
            originOptions: [], // 区下拉框数据
            /*
                数据格式有要求：
                    一级： 110000
                    二级： 110100
                    三级： 110101
                每级占2位数，有层次和顺序
            */
            options: [
                {
                    "value": "110000",
                    "label": "北京市",
                    "children": [
                        {
                            "value": "110100",
                            "label": "北京市区",
                            "children": [
                                {
                                    "value": "110101",
                                    "label": "东城区"
                                },
                                {
                                    "value": "110102",
                                    "label": "西城区"
                                },
                                {
                                    "value": "110103",
                                    "label": "朝阳区"
                                },
                                {
                                    "value": "110104",
                                    "label": "海淀区"
                                }
                            ]
                        },{
                            "value": "110200",
                            "label": "北京郊区",
                            "children": [
                                {
                                    "value": "110205",
                                    "label": "大兴区"
                                },
                                {
                                    "value": "110206",
                                    "label": "通州区"
                                },
                                {
                                    "value": "110207",
                                    "label": "顺义区"
                                },
                                {
                                    "value": "110208",
                                    "label": "昌平区"
                                },
                                {
                                    "value": "110209",
                                    "label": "房山区"
                                }
                            ]
                        }
                    ]
                }, {
                    "value": "130000",
                    "label": "河北省",
                    "children": [
                        {
                            "value": "130100",
                            "label": "石家庄市",
                            "children": [
                                {
                                    "value": "130102",
                                    "label": "长安区",
                                },{
                                    "value": "130104",
                                    "label": "新华区",
                                },{
                                    "value": "130107",
                                    "label": "藁城区",
                                }
                            ]
                        },
                        {
                            "value": "130200",
                            "label": "唐山市",
                            "children": [
                                {
                                    "value": "130202",
                                    "label": "古治区",
                                },{
                                    "value": "130204",
                                    "label": "路北区",
                                },{
                                    "value": "130207",
                                    "label": "路南区",
                                }
                            ]
                        },{
                            "value": "131100",
                            "label": "衡水市",
                            "children": [
                                {
                                    "value": "131102",
                                    "label": "桃城区",
                                },{
                                    "value": "131104",
                                    "label": "冀州区",
                                },{
                                    "value": "131107",
                                    "label": "枣强县",
                                },{
                                    "value": "131110",
                                    "label": "安平县",
                                }
                            ]
                        },
                    ]
                }, {
                    "value": "140000",
                    "label": "山西省",
                    "children": [
                        {
                            "value": "140100",
                            "label": "太原市",
                            "children": [
                                {
                                    "value": "140105",
                                    "label": "小店区"
                                },{
                                    "value": "140107",
                                    "label": "迎泽区"
                                },{
                                    "value": "140109",
                                    "label": "万柏林区"
                                }
                            ]
                        },{
                            value: "140300",
                            label: "阳泉市",
                            children: [
                                {
                                    value: "140305",
                                    label: "城区"
                                },{
                                    value: "140307",
                                    label: "矿区"
                                },{
                                    value: "140309",
                                    label: "郊区"
                                },{
                                    value: "140311",
                                    label: "平定县"
                                }
                            ]
                        }
                    ]
                }
            ],
            sendParent: []
        }
    },
    computed: {
        isDisabled: function(){
            var _this = this;
            var disabledValue = [];
            if(_this.valueProvince==''){
                disabledValue.push(true);
                disabledValue.push(true);
            }else if(_this.valueProvince!='' && _this.valueCity==''){
                disabledValue.push(false);
                disabledValue.push(true);
            }else if(_this.valueProvince!='' && _this.valueCity!=''){
                disabledValue.push(false);
                disabledValue.push(false);
            }
            return disabledValue;
        }
    },
    created() {
        this._getJsonData();
    },
    methods: {
        // 选择省
        changeProvince(val) {
            this.provinceList.forEach((province, index) => {
                if (val.toString() === this.provinceList[index].value) {
                    // this.cityOptions = this.provinceList[index].children
                    // this.valueCity = this.provinceList[index].children[0].value
                    // this.originOptions = this.provinceList[index].children[0].children
                    // this.valueOrigin = this.provinceList[index].children[0].children[0].value
                    this.cityOptions = this.provinceList[index].children;
                    this.valueCity = '';
                    this.valueOrigin = '';
                    // this.cascadeValue();
                    this.cascadeValueObj(0,province);
                    return;
                }
            })
        },
        // 选择市
        changeCity(val) {
            this.cityList.forEach((city, index) => {
                if (val.toString() === this.cityList[index].value) {
                    // this.originOptions = this.cityList[index].children
                    // this.valueOrigin = this.cityList[index].children[0].value
                    this.originOptions = this.cityList[index].children;
                    this.valueOrigin = '';
                    // this.cascadeValue();
                    this.cascadeValueObj(1,city);
                    return;
                }
            })
        },
        // 选择区
        changeOrigin(val) {
            this.originList.forEach((origin, index) => {
                if (val.toString() === this.originList[index].value) {
                    this.valueOrigin = val;
                    // this.cascadeValue();
                    this.cascadeValueObj(2, origin);
                    return;
                }
            })
        },
        // 处理数据列表
        _getJsonData() {
            this.provinceList = [];
            this.cityList = [];
            this.originList = [];
            var res = this.data;
            res.forEach((item) => {
                // eslint-disable-next-line no-mixed-spaces-and-tabs
                this.provinceList.push({
                    value: item.value,
                    label: item.label,
                    children: []
                });
                for (let i = 0; i < item.children.length; i++) {
                    this.cityList.push({
                        value: item.children[i].value,
                        label: item.children[i].label,
                        children: []
                    });
                    for (let j = 0; j < item.children[i].children.length; j++) {
                        this.originList.push({
                            value: item.children[i].children[j].value,
                            label: item.children[i].children[j].label
                        })
                    }
                }
            });
            for (let index in this.provinceList) {
                for (let index1 in this.cityList) {
                    if (this.provinceList[index].value.slice(0, 2) === this.cityList[index1].value.slice(0, 2)) {
                        this.provinceList[index].children.push(this.cityList[index1])
                    }
                }
            }
            for (let item1 in this.cityList) {
                for (let item2 in this.originList) {
                    if (this.originList[item2].value.slice(0, 4) === this.cityList[item1].value.slice(0, 4)) {
                        this.cityList[item1].children.push(this.originList[item2])
                    }
                }
            }
        },
        cascadeValueObj: function (index,val) {
            // eslint-disable-next-line no-mixed-spaces-and-tabs
        	if(index == 0){
                this.sendParent[0] = {
                    "value": val.value,
                    "label": val.label
                };
                this.sendParent[1] = {};
                this.sendParent[2] = {};
            }else if(index == 1){
                this.sendParent[1] = {
                    "value": val.value,
                    "label": val.label
                };
                this.sendParent[2] = {};
            }else if(index == 2){
                this.sendParent[2] = {
                    "value": val.value,
                    "label": val.label
                };
            }
            // eslint-disable-next-line no-mixed-spaces-and-tabs
		    this.$emit('cascadeShow',this.sendParent);
        }
        // cascadeValue: function () {
        //     this.sendParent.push(this.valueProvince);
        //     this.sendParent.push(this.valueCity);
        //     this.sendParent.push(this.valueOrigin);
        //     this.$emit('cascadeShow',this.sendParent);
        // }
    }
}
</script>
<style lang="scss" scoped>
.el-select.el-select--medium{
	width: calc((100% - 16px)/3) !important;
	&:nth-child(2){
		margin: 0 8px;
	}
}
</style>
