<!--
    ===============
    TimePicker 组件
    ===============
    功能：    时间选择器，用来选择当前的时间或者指定时间；
    实现原因：PICC有的系统要求小时需要有24小时显示(系统默认，包括浏览器端javascript小时就没有获取24时的时候)，
            所以该功能无法在elementUI组件中使用；
    实现方式： 1. 获取当前时间的时分秒，并修改其数据类型为字符串；
              2. 时分秒分三列，小时 00-60， 分钟 00-60，秒 00-60
              3. 通过点击数字来确定选择的时分秒，然后通过js控制滚动条来让选择的数字放在中间位置；

 -->
<template id="">
    <div class="picc-time-picker">
        <el-input v-model="value"
        placeholder="请输入内容"
        @focus="timerPick"
        :disabled="disabled"
        readonly
         />
        <div class="picc-timer-picker-box" v-show="timerPickShow">
            <el-scrollbar>
                <ul class="hour">
                    <li v-for = '(hour, index) in hours' :key = 'index' :data-id="hour"
                        @click="timePick(hour, 'hour', $event)"
                    >
                        {{ hour }}
                    </li>
                </ul>
            </el-scrollbar>
            <el-scrollbar>
                <ul class="minute">
                    <li v-for = '(minute, index) in minutes' :key = 'index' :data-id="minute"
                        @click="!is24 && timePick(minute, 'minute', $event)"
                    >
                        {{ minute }}
                    </li>
                </ul>
            </el-scrollbar>
            <el-scrollbar>
                <ul class="second">
                    <li v-for = '(second, index) in seconds' :key = 'index' :data-id="second"
                        @click="!is24 && timePick(second, 'second', $event)"
                    >
                        {{ second }}
                    </li>
                </ul>
            </el-scrollbar>
            <div class="picc-timer-picker-border"></div>
        </div>
    </div>
</template>
<script>
export default {
    name:'TimePicker',
    data () {
        return {
            hours: [],
            minutes: [],
            seconds: [],
            hour: '',
            minute: '',
            second: '',
            is24: false,
            timerPickShow: false
        }
    },
    props: {
        value: {
            type: String,
            // required: true,
            default: function() {
                return ('0' + new Date().getHours()).slice(-2) + ':'+ ('0' + new Date().getMinutes()).slice(-2) + ':'+ ('0' + new Date().getSeconds()).slice(-2);
            }
        },
        disabled: {
            type: Boolean,
            default: function() {
                return false
            }
        }
    },
    created () {

        this.hours = this.calcTime('hour');
        this.minutes = this.calcTime('minute');
        this.seconds = this.calcTime('second');
        if (this.value == '') {
            this.hour = ('0' + new Date().getHours()).slice(-2);
            this.minute = ('0' + new Date().getMinutes()).slice(-2);
            this.second = ('0' + new Date().getSeconds()).slice(-2);
        } else {
            this.hour = this.value.split(':')[0];
            this.minute = this.value.split(':')[1];
            this.second = this.value.split(':')[2];
        }

    },
    mounted () {
        let _that = this;
        function checkTimePickClick(dom){
            if (dom.parentNode) {
                if (dom.parentNode.classList) {
                    if (!dom.parentNode.classList.contains('picc-time-picker')) {
                        checkTimePickClick(dom.parentNode)
                    }
                } else {
                    _that.timerPickShow = false;
                }
            }
        }

        document.body.addEventListener('click', (e) => {
            checkTimePickClick(e.target)
        }, true)
    },

    methods: {
        //
        timerPick() {
            this.timerPickShow = !this.timerPickShow;
            if (this.timerPickShow) {
                this.$nextTick( () => {
                    this.timePick (
                        this.hour,
                        'hour',
                        document.querySelectorAll(`.hour li[data-id="${this.hour}"]`)[0]
                    );
                    this.timePick (
                        this.minute,
                        'minute',
                        document.querySelectorAll(`.minute li[data-id="${this.minute}"]`)[0]
                    );
                    this.timePick (
                        this.second,
                        'second',
                        document.querySelectorAll(`.second li[data-id="${this.second}"]`)[0]
                    );
                })
            }
        },
        calcTime(type) {
            let timeArr = [], num;
            if (type == 'hour') {
                num = 25;
            } else {
                num = 60;
            }
            for (let i = 0; i < num; i++) {
                timeArr.push( ('0' + i).slice(-2) )
            }
            return timeArr;
        },
        timePick (val, type, e) {

            if (type == 'hour' && val == '24') {

                this.minute = '00';
                this.second = '00';
                this.timePick (this.minute, 'minute',  document.querySelectorAll(`.minute li[data-id="00"]`)[0]);
                this.timePick (this.second, 'second',  document.querySelectorAll(`.second li[data-id="00"]`)[0]);
                this.is24 = true;
            } else {
                this.is24 = false;
            }

            e = e.target ? e : e.target = e;

            let li = [...e.target.parentNode.children];

            li.forEach( (item) => {
                item.classList.remove('selected')
            })

            e.target.classList.add('selected');

            let currentTimeLine = e.target.parentElement.offsetParent.childNodes[0];

            currentTimeLine.scrollTo(0, val * 32 );

            this[type] = val;

        },

    },
    computed: {
        timer () {
            return this.hour + ':' + this.minute + ':' + this.second;
        }
    },
    //
    watch: {
        timer (newVal, oldVal) {
            if (oldVal !== newVal) {
                this.$emit('input', newVal)
            }
        }
    }
}
</script>
<style lang="scss">
    .picc-time-picker {
        width: 180px;
        position: relative;
    }
    .picc-timer-picker-box {
        width: 180px;
        height: 224px;
        display: flex;
        background: #fff;
        top: 50px;
        position: absolute;
        box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
        ul {
            overflow: hidden;
            margin: 0;
            padding: 0;
            list-style: none;
            width: 60px;
            li {
                height: 32px;
                line-height: 32px;
                font-size: 12px;
                text-align: center;
                &.selected {
                    color: #1c6cfc;
                    font-weight: bold;
                    font-size: 12px;
                }
            }
        }
        .el-scrollbar__wrap {
            overflow-x: hidden;
            padding: 96px 0;
        }
        &:before {
            content: "";
            display: block;
            position: absolute;
            width: 10px;
            top: -10px;
            left: 20px;
            border-bottom: 10px solid #fff;
            border-left: 10px solid transparent;
            border-right: 10px solid transparent;
        }
    }
    .picc-timer-picker-border {
        width: 100%;
        height: 32px;
        position: absolute;
        background: rgba(0,0,0,0);
        top: 96px;
        border: 1px solid #eee;
        // box-shadow: inset 0px 0px 11px 2px rgba(0, 0, 0, 0.1);
        border-left: 0;
        border-right: 0;

    }
</style>
