<template>
    <span class="picc_tab">
        <div :class="['container',{mini:size ==='mini',selected:selected,sub_container:type === 'status' || type === 'addItem',forbid:type ==='addItem' && state ==='reject',chose:type ==='addItem' && state ==='chose'}]">
            <div>
                <span :class="['title',{isStatus:type === 'status',isAddItem:type ==='addItem',min_title:type === 'status' && title.length > 5}]">{{title}}</span>
            </div>
            <div v-if="type !='addItem'" :class="type === 'number'? 'right_part_num' : 'right_part_sta'">
                <span class="right info" v-if="type === 'number'">
                    <span class="num">{{num}}</span>
                    <span class="unit">条</span>
                </span>
                <span class="right status" v-if="type === 'status' && state === ''"></span>
                <span class="right status reject" v-if="type === 'status'&& state ==='reject'">
                    <span></span>
                </span>
                <span class="right status success" v-if="type === 'status'&& state ==='success'">
                    <span></span>
                </span>
                <span class="right status waiting" v-if="type === 'status'&& state ==='waiting'">
                    <span></span>
                </span>
            </div>
        </div>
    </span>

</template>

<script>
export default {
    name: "TabItem",
    props:{
        size:{type:String},
        title:{type:String},
        num:{type:Number,default:0},
        type:{type:String},
        selected:{type:Boolean,default: false},
        state:{type:String,default:''}
    }
}
</script>

