            white-space: nowrap;
            &.info{
                z-index: 1;
                /*&:before{
                    position: absolute;
                    content: '';
                    background-color: #edeffb;
                    min-width: 180px;
                    height: 180px;
                    border-radius: 90px;
                    top: -30px;
                    left:-35px;
                    z-index: -1;
                }*/
            }
            &.status{
