                width: 16px;
                height: 16px;
                top: -8px;
                right: 12px;
                span{
                    display: inline-block;
                    width: 16px;
                    height: 16px;
                }
            }

        }
        .title {
            width: 48px;
            max-height: 32px;
            word-wrap: break-word;
            line-height: 16px;
            margin-left: 16px;
            font-family: 微软雅黑，Helvetica;
            &.isStatus{
                font-size: 14px;
                width: 70px;
                z-index: 2;
                position: relative;
            }
            &.isAddItem{
