<template>
    <div class="picc-navbar" :class="{'fixed-nav':isFixed}">
        <div class="crumb">
            <slot name="crumb"></slot>
        </div>
        <div class="right-menu">
            <div class="navbar-search">
                <slot name="search-input"></slot>
            </div>
            <div class="navbar-operation">
<!--                <i class="el-icon-message-solid"/>-->
                <span v-show="newInfo" class="new-info-notice"></span>
                <el-popover ref="new-info-pop"
                        placement="bottom"
                        width="372"
                        popper-class="picc-popover-message"
                        trigger="hover">
                    <div class="picc-popover-content">
                        <slot name="bell-message"/>
                    </div>
                    <div class="picc-popover-operation">
                        <slot name="bell-operation"/>
                    </div>
                    <i class="picc-icon picc-icon-bell" slot="reference"></i>
                </el-popover>
            </div>
            <div class="navbar-brief-info">
                <img :src="personInfo.picture" class="user-avatar" v-popover:changeworkState>
<!--                <span :class="['work-state',{'working':personInfo.state}]" v-popover:changeworkState></span>-->
                <span :class="['work-state',{'working':state}]"></span>
                <el-popover popper-class="work-state-popover"
                        ref="changeworkState"
                        placement="right-start"
                        trigger="click" :visible-arrow="false" v-model="popoverShow" :offset="60">
                    <div class="work-state-pop">
                        <ul>
                            <li v-for="(value,index) in workState" :key="index" @click="selectState(index)" :class="{ 'selected':value.select}">
                                <span :class="['state-select',{ 'selected':value.select}]"></span>
                                <span :class="['state-dot',{'selected':value.select}]"></span>
                                <span class="state-info">{{value.lable}}</span>
                            </li>
                        </ul>
                    </div>
                </el-popover>
                <span class="navbar-brief-info-department">{{personInfo.position}}</span>
                <span class="navbar-brief-info-name">{{personInfo.name}}</span>
            </div>
            <el-dropdown class="avatar-container right-menu-item hover-effect" trigger="click">
                <div class="avatar-wrapper">
                    <i class="el-icon-caret-bottom"/>
                </div>
                <el-dropdown-menu slot="dropdown" class="picc-nav-dropdown-menu">
                    <slot name="dropdown-menu-list"></slot>
                    <el-dropdown-item divided>
                        <span style="display:block;" v-popover:loginOut>退出账号</span>
                        <el-popover popper-class="login-out-popover"
                                ref="loginOut"
                                placement="top"
                                    width="360"
                                v-model="loginOut" :visible-arrow="false">
                            <p><i class="picc-icon picc-icon-error-sm login-out-icon"></i>您是否确定退出当前账号</p>
                            <div style="text-align: right; margin: 0 24px 24px 0">
                                <el-button round size="mini" @click="loginOut = false" style="width: 88px">取消 </el-button>
                                <el-button round type="primary" size="mini" @click="loginOut = false">确定退出</el-button>
                            </div>
                        </el-popover>
                    </el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </div>
    </div>
</template>

<script>

export default {
    name: 'Navbar',
    props: {
        personInfo: {
            type: Object,
            default: () => {
                return {
                    name: '孙大圣',
                    position: '部门经理',
                    state:true,
                    newInfo:false,
                    picture: 'https://ss0.bdstatic.com/94oJfD_bAAcT8t7mm9GUKT-xh_/timg?image&quality=100&size=b4000_4000&sec=1574822238&di=f63eb9d34c9ea38a788da259986fbdab&src=http://pic.58pic.com/58pic/13/14/16/37J58PICWTD_1024.jpg'
                }
            }
        },
        newInfo:{
            type:Boolean,
            default:false
        },
        isFixed:{
            type:Boolean,
            default:false
        },
        workState:{
            type:Array,
            default:() => {
                return [
                    {select:true,lable:'上班'},
                    {select:false,lable:'下班'}
                ]
            }
        }
    },
    data() {
        return {
            willSelect:false,
            /*workState:[
                {select:true,lable:'上班'},
                {select:false,lable:'下班'}
            ],*/
            popoverShow:false,
            loginOut:false,
            state:true
        }
    },
    methods: {
        selectState(index){
            this.popoverShow = false;
            this.state = index ===0 ? true : false;
            this.workState = this.workState.map((v,i) =>{
                v.select = i===index ? true : false;
                return v
            })

        }
    }
}
</script>

<style lang="scss">
    .picc-navbar {
        height: 50px;
        width: 100%;
        min-width: 850px;
        overflow: hidden;
        background: #fff;
        box-shadow: 0 1px 4px rgba(0, 21, 41, .08);
        text-align: left;
        &.fixed-nav{
            position: fixed;
            top: 0;
        }

        .crumb {
            margin-left: 24px;
            float: left;
            height: 100%;
            line-height: 50px;
        }

        & > .right-menu {
            float: right;
            height: 100%;
            line-height: 50px;

            > * {
                float: left;
            }


            &:focus {
                outline: none;
            }

            & > .navbar-search {
                margin-right: 20px;
                width: 380px;
                height: 100%;
                opacity: 0.6;


                /deep/ .el-input__inner {
                    font-size: 12px;
                }
            }

            .navbar-operation {
                margin-right: 16px;
                width: 20px;
                position: relative;

                .new-info-notice{
                    width: 6px;
                    height: 6px;
                    background-color: red;
                    display: inline-block;
                    border-radius: 50%;
                    position: absolute;
                    left: 10px;
                    top: 16px;
                }

                .picc-icon-bell{
                    vertical-align: middle;
                    cursor: pointer;
                }
            }

            .navbar-brief-info {
                position: relative;

                img {
                    margin-right: 16px;
                    width: 28px;
                    height: 28px;
                    vertical-align: middle;
                    border-radius: 50%;
                    cursor: pointer;
                }

                .work-state {
                    width: 12px;
                    height: 12px;
                    border-radius: 50%;
                    position: absolute;
                    left: 16px;
                    top: 28px;
                    background-color: #DDDDDD;
                    &.working{
                        background-color: #42DB2A;
                    }
                    &:focus{
                        border: none;
                    }
                }

                .navbar-brief-info-department,.navbar-brief-info-name {
                    margin-right: 8px;
                    font-size: 12px;
                }
            }

            .right-menu-item {
                display: inline-block;
                height: 100%;
                font-size: 18px;
                color: #5a5e66;
                vertical-align: text-bottom;

                &.hover-effect {
                    margin-right: 24px;
                    width: 20px;
                    cursor: pointer;
                    transition: background .3s;
                }
            }

            .avatar-container {
                margin-right: 30px;

                .avatar-wrapper {
                    margin-top: 5px;
                    position: relative;

                    .user-avatar {
                        cursor: pointer;
                        width: 40px;
                        height: 40px;
                        border-radius: 10px;
                    }

                    .el-icon-caret-bottom {
                        cursor: pointer;
                        position: absolute;
                        right: 3px;
                        top: 14px;
                        font-size: 12px;
                    }
                }
            }
        }
    }
    .work-state-popover{
        padding: 0;
        min-width: 92px;
        margin-left: 8px !important;
        margin-top: -13px !important;

        .work-state-pop{

            ul{
                padding: 0;
                margin: 0;

                li{
                    height: 32px;
                    line-height: 32px;

                    &.selected{
                        background-color: #EAEDFC;
                    }

                    span{
                        display: inline-block;
                        margin-right: 8px;
                    }

                    .state-select{
                        width: 20px;
                        height: 20px;
                        vertical-align: middle;
                        margin-left: 8px;
                        &.selected{
                            background: url('../../assets/icons/success-square.png') no-repeat center center ;
                            background-size: 12px;
                        }
                    }
                    .state-dot{
                        width: 8px;
                        height: 8px;
                        background-color: #DDDDDD;
                        border-radius: 50%;
                        &.selected{
                            background-color: #40D22A;
                        }
                    }
                    .state-info{
                        margin-right: 16px;
                    }
                }
            }
        }
    }
    .login-out-popover{
        margin: 0;
        padding: 0;
        top: calc(50% - 72px) !important;
        left: calc(50% - 180px) !important;
        p{
            margin-top: 38px;
            margin-bottom: 30px;
            font-size: 16px;
            font-weight: bold;
        }

        .login-out-icon{
            margin-left: 24px;
            margin-right: 21px;
            vertical-align: middle;
        }
    }
    .picc-popover-message{
        padding:0 !important;
        margin-top: 24px !important;
        .popper__arrow{
            display: block !important;
        }
        .picc-popover-content{}
        .picc-popover-operation{}
    }
    .picc-nav-dropdown-menu{
        width: 112px;
        margin-top: -16px !important;

        a{
            display: block;
            line-height: 36px;
            padding-left: 20px;
            margin: 0;
            font-size: 12px;
            color: #606266;
            cursor: pointer;
            outline: none;
            &:hover{
                background-color: #ecf5ff;
                color: #66b1ff;
            }
        }

        .el-popover__reference{
            font-size: 12px;
        }

        .popper__arrow{
            display: none;
        }
    }

</style>
