<template>
    <div class="picc-full-screen-bar">
        <template v-for="(item,index) in showdata">
            <div class="red-zone" :key="index" v-if="index===0">
                <div class="show-data">
                    <span class="main-data">{{item.value}}</span>
                    <span class="sub-data">{{item.subValue}}</span><br>
                    <span class="describ-data">{{item.title}}</span>
                </div>
            </div>
            <div class="black-zone" :key="index" v-else>
                <div class="show-data">
                    <span class="main-data">{{item.value}}</span>
                    <span class="sub-data">{{item.subValue}}</span><br>
                    <span class="describ-data">{{item.title}}</span>
                </div>
                <div class="black-image" v-if="item.icons.type === 'none'">
                </div>
                <div class="black-image" v-if="item.icons.type === 'image'">
                    <!-- <img src="../../assets/zhexian.png"> -->
                </div>
                <div class="black-image" v-if="item.icons.type === 'trend'">
                    <!-- <svg-icon :icon-class="item.icons.value" /> -->
                    <i :class="'picc-icon picc-icon-'+item.icons.value"></i>
                </div>
            </div>
        </template>
    </div>
</template>
<script>
export default {
    name:'FullScreenBar',
    props:{
        showdata:{
            type:Array,
            default:()=>[]
        }
    }
}
</script>
<style lang="scss">
.picc-full-screen-bar{
    background: #292B34;
    height: 100px;
    overflow: hidden;
    .red-zone{
        min-width:180px;
        height: 100%;
        text-align: center;
        float: left;
        position: relative;
        margin-right:32px;
        &::before{
            content: '';
            position: absolute;
            width: 360px;
            height: 360px;
            background: #D5402C;
            right:-15px;
            top:-60px;
            border-top-right-radius: 180px;
            border-bottom-right-radius: 180px;
            z-index: 0;
            border-color: red blue #000 yellow;
        }
        & .show-data{
            position: relative;
            margin: auto;
            margin:24px 54px 0px 36px;
            z-index: 100;
        }
        & .main-data{
            width: 100%;
            text-align: center;
            font-family: HelveticaNeue-Medium;
            font-size: 32px;
            color: #FFFFFF;
            letter-spacing: 0;
            line-height: 32px;
        }
        & .sub-data{
            font-family: HelveticaNeue-Medium;
            font-size: 14px;
            color: #FFFFFF;
            letter-spacing: 0;
            line-height: 14px;
        }
        & .describ-data{
            display: block;
            margin-top:8px;
            width: 100%;
            text-align: center;
            font-family: MicrosoftYaHeiUI;
            font-size: 12px;
            color: #FFFFFF;
            line-height: 12px;
        }
    }
    .black-zone{
        position: relative;
        color:#FFFFFF;
        min-width:108px;
        height: 100%;
        float: left;
        overflow: hidden;
        margin-right:32px;
        & .show-data{
            float: left;
            margin:24px auto;
            & .main-data{
                width: 100%;
                text-align: center;
                font-family: HelveticaNeue-Medium;
                font-size: 32px;
                color: #FFFFFF;
                letter-spacing: 0;
                line-height: 32px;
            }
            & .sub-data{
                font-family: HelveticaNeue-Medium;
                font-size: 14px;
                color: #FFFFFF;
                letter-spacing: 0;
                line-height: 14px;
            }
            & .describ-data{
                display: block;
                margin-top:8px;
                width: 100%;
                text-align: center;
                font-family: MicrosoftYaHeiUI;
                font-size: 12px;
                color: #FFFFFF;
                line-height: 12px;
            }
        }

        & .black-image{
            float: left;
            width: auto;
            height: 32px;
            // background:yellow;
            margin-top: 24px;
            text-align: left;
            margin-left: 4px;
        }
    }
}

</style>
