<template>
    <div class="picc-unfold-switch" ref="unfold">
        <!-- <div v-show="isOpen" class="unfold-switch--border" /> -->
        <div :class=" isOpen ? 'unfold-switch--content is-open': 'unfold-switch--content'">
            <slot/>
        </div>
        <div class="operation-btn" >
            <el-button 
            type="text" 
            @click="isOpen=!isOpen; $parent.collapse(isOpen)" 
            :icon="isOpen ? 'el-icon-caret-top' : 'el-icon-caret-bottom'" 
            class="picc-button--icon btn-group" 
            >
                {{isOpen ? '收起' : '展开'}}
            </el-button>
        </div>
    </div>
</template>
<script>
export default {
    name:'piccFold',
    props: {
        createIsOpen:{
            type: Boolean,
            default:false
        },

    },
    data(){
        return {
            isOpen:false
        }
    },
    create(){
        this.isOpen = this.createIsOpen
    }
    
}
</script>
<style lang="scss">
.picc-unfold-switch{
    position: relative;
    & > .unfold-switch--content{
        padding: 0 2px 12px;
        background: #f7f8fe;
        display: none;

        & + .operation-btn {
            i {
                color: #B1B2BC;
            }
        }
        &.is-open {
            display: block;
            & + .operation-btn {
                i {
                    color: #1c61fc;
                }
            }
            &:before {
                content: "";
                margin:0 16px;
                border-top: 1px dashed rgba(178,190,255,0.50);;
                display: block;
            }
        }
    }
    & > .operation-btn{
        height: 36px;
        text-align: center;
        cursor: pointer;
        // margin-top: 12px;
        .el-button {
            font-size: 12px;    
            span {
                color: #65677a;
            }
        }
        .btn-group{
            height: 20px;
            line-height: 20px;
            & > span{
                height: 20px;
                line-height: 20px;
                margin-left: 0px;
                display: inline-block;
                vertical-align: middle;
                float: right;
            }
            & > i{
                width: 20px;
                height: 20px;
                line-height: 20px;
                display: inline-block;
                vertical-align: middle;
                float: right;
            }
        }
    }
}
</style>