      <div class="picc-sidebar-sub" :key="index" v-for="(item, index) in newRoutesSort">
          <div class="picc-sidebar-sub--item" :key = "subIndex" v-for= "(subItem, subIndex) in item">
              <div class="" v-if="!subItem.children && !subItem.hidden">
                  <router-link :to="(path == '/' ? '' : path)+ '/'+subItem.path" :title="subItem.meta.title">{{ subItem.meta.title }}</router-link>
              </div>
              <div class="picc-sidebar-sub--item has-sub-title" v-else>
                  <h2 :title="subItem.meta.title"> {{ subItem.meta.title }} </h2>
                  <div v-for="(child, childIndex) in subItem.children" :key="childIndex" class="">
                    <router-link v-if = '!child.hidden' :to="(path == '/' ? '' : path) + '/' + subItem.path +'/' + child.path" :title="child.meta.title" >{{ child.meta.title }} </router-link>
                  </div>
              </div>
          </div>
      </div>
    </div> -->

  </div>
</template>
<script>
export default {
    //
    name: 'SidebarItem',
    //
    props: {
        path: {
            type: String,
            required: true,
            default: function() {
                return ''
            }
        },
        meta: {
            type: Object,
            required: true
        },
        route: {
            type: Object,
            required: true
        }
    },
    created() {},
    data () {
        return {
            newRoutesSort: []
        }
    },
    mounted() {
    // this.$nextTick(() => {
    //   const subMenuBoxValidHeight = document.body.clientHeight - 200
    //   const subMenuItems = document.querySelectorAll('.picc-sidebar-sub .picc-sidebar-sub--item')
    //
    //   if (this.$refs.sidebarSub) {
    //     this.subItemsDivide(subMenuBoxValidHeight, subMenuItems)
    //   }
    // })
    //
        // 每个连接的高度时32px
        let validHeight = document.body.clientHeight - 200;
        //
        if (this.route.children) {
            //
            let height = 0;
            let newRoutesSort = [[]];

            for (let item of this.route.children) {
                //
                if (item.children) {
                    let subHeights = 64;
                    item.children.forEach( () => {
                        subHeights += 32;
                    })
                    height += subHeights;
                    //
                    if (validHeight - height < 0) {
                        //
                        newRoutesSort.push([item]);
                        subHeights = 0;

                    } else {
                        newRoutesSort[newRoutesSort.length - 1].push(item)
                    }
                } else {

                    height += 32;
                    //
                    if (validHeight - height < 0) {
                        // console.log(item)
                        newRoutesSort.push([item]);
                        height = 0;
                    } else {
                        newRoutesSort[newRoutesSort.length - 1].push(item)
                    }
                }

            }
            this.newRoutesSort = newRoutesSort;
            // console.log(this.newRoutesSort)
        }
    },
    methods: {
        /*
         * 对所有子菜单列表进行分割，通过判断相加的子菜单列表高度是否超出计算后有效空间的高度，
         * 则创建新的一列，以此类推，通过递归方式进行对比划分
         @ subMenuBoxValidHeight 有效高度，根据窗口高度减去 200
         @ subItems 子菜单所有列表
         @ subState 默认从1开始，第二个二级菜单项开始
        */
        subItemsDivide(subMenuBoxValidHeight, subItems, subState = 1) {
            let menuItemsHeight = 0
            const newItemsColumn = document.createElement('div')
            for (const item of [...subItems]) {
                menuItemsHeight += item.clientHeight
                if (subMenuBoxValidHeight - menuItemsHeight < 0) {
                    newItemsColumn.setAttribute('class', 'picc-sidebar-sub');
                    newItemsColumn.classList.add(`subItems-${subState}`)
                    newItemsColumn.append(item)
                }
            }
            document.querySelector('.picc-sidebar-item > div').append(newItemsColumn)
            if (document.querySelector('.subItems-' + subState)) {
                this.subItemsDivide(
                    subMenuBoxValidHeight,
                    document.querySelector('.subItems-' + subState).children,
                    ++subState
                )
            }
        }
    }
}
</script>
