<template>
    <div class="picc-anchor-point" :style="'top:'+top+';right:'+right+';'">
        <div v-for="(item,index) in pointList" :key="index">
            <div class="anchoe-point-option" v-if="index === 0" @click="jumpTo(item)">
                <div class="sign">
                    <div class="line-bottom"></div>
                    <div class="dot"></div> 
                </div>
                <div class="title">{{item.title}}</div>
            </div>
            <div class="anchoe-point-option" v-if="index !== 0 && index !== pointList.length-1"  @click="jumpTo(item)">
                <div class="sign">
                    <div class="line-middle"></div>
                    <div class="dot"></div> 
                </div>
                <div class="title">{{item.title}}</div>
            </div>
            <div class="anchoe-point-option" v-if="index === pointList.length-1"  @click="jumpTo(item)">
                <div class="sign">
                    <div class="line-top"></div>
                    <div class="dot"></div> 
                </div>
                <div class="title">{{item.title}}</div>
            </div>
        </div>
        
    </div>
</template>
<script>
export default {
    name:"AnchorPoint",
    props:{
        pointList:{
            type:Array,
            default:()=>[]
        },
        top:{
            type:String,
            default:'0px'
        },
        right:{
            type:String,
            default:'0px'
        }
    },
    data(){
        return {
            distance:[]
        }
    },
    created(){

    },
    mounted (){
        this.$nextTick(()=>{
            document.getElementsByClassName('anchoe-point-option')[0].classList.add('active')
            for(let i=0;i<this.pointList.length;i++){
                this.distance.push(document.getElementById(this.pointList[i].id).offsetTop)
            }
            window.addEventListener('scroll', ()=> {
                let scrollBarToTop = document.documentElement.scrollTop
                let achors = document.getElementsByClassName('anchoe-point-option')
                for(let i=this.distance.length-1;i>=0;i--){
                    if(this.distance[i]<=scrollBarToTop){
                        for(let j=0;j<achors.length;j++){
                            achors[j].classList.remove("active")
                        }
                        achors[i].classList.add("active")
                        return
                    }
                }
            }) 
        })
        
    },
    methods:{
        jumpTo(item){
            let achors = document.getElementsByClassName('anchoe-point-option')
            for(let j=0;j<achors.length;j++){
                achors[j].classList.remove("active")
            }
            document.getElementById(item.id).classList.add("active")
            window.scrollTo(0,document.getElementById(item.id).offsetTop)
        }
    }
}
</script>
<style lang="scss">
.picc-anchor-point{
    width:128px;
    position: fixed;
    & .anchoe-point-option{
        width:128px;
        height:20px;
        line-height: 20px;
        padding-left: 16px;
        text-align: left;
        cursor:pointer;
    }
    .sign{
        width:20px;
        height: 20px;
        display: inline-block;
        position: relative;
    }
    .dot{
        display: inline-block;
        background: #CDCDCD;
        width:4px;
        height: 4px;
        border-radius: 2px;
        margin:8px;
        position: relative;
        z-index: 1;
    }
    .active{
        .title{
            font-weight: bold;
        }
        .dot{
            width:8px !important;
            height: 8px !important;
            border-radius: 4px !important;
            background-image: linear-gradient(59deg, #4251E0 0%, #1C61FC 100%) !important;
            margin:6px;
            position: relative;
            z-index: 1;
        }
        
    }
    .line-top{
        height: 10px;
        width:0px;
        opacity: 0.3;
        border-left: 1px dotted #292B34;
        position: absolute;
        left:10px;
        z-index: 0;
    }
    .line-middle{
        height: 20px;
        width:0px;
        opacity: 0.3;
        border-left: 1px dotted #292B34;
        position: absolute;
        left:10px;
        z-index: 0;
    }
    .line-bottom{
        height: 10px;
        width:0px;
        opacity: 0.3;
        border-left: 1px dotted #292B34;
        position: absolute;
        left:10px;
        top:10px;
        z-index: 0;
    }
    .title{
        display: inline-block;
        font-family: MicrosoftYaHeiUI;
        font-size: 12px;
        color: #292B34;
        line-height: 12px;
        vertical-align: text-top;
        width:92px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
}
</style>