<template>
    <div class="picc-form-card">
        <!-- 内页卡片内标题栏 -->
        <div class="picc-inside-titleBar" v-if="title!=''">
            <div class="title-area">{{title}}</div>
            <div class="operation-area">
                <slot name="operation"></slot>
            </div>
        </div>

        <div class="picc-el-form">
            <slot></slot>
        </div>
        <fold :createIsOpen="createIsOpen">
            <slot name="expanderBox"></slot>
        </fold>
    </div>
</template>
<script>
import fold from './fold'
export default {
    name:'FormCard',
    components:{
        fold
    },
    props: {
        title:{
            type: String,
            default: ''
        },
        createIsOpen:{
            type: Boolean,
            default:false
        },
        // collpase: {
        //     type: Function
        // }
    },
    methods:{
        remove(){
            this.$emit('remove')
        },
        collapse (e) {
            this.$emit('collapse', e)
        }
    }
}
</script>

<style lang="scss" scoped>
.picc-form-card {
    // margin-top:30px;
    border: 1px solid #cdcdcd;
    border-radius: 4px;
}
/*内页卡片内标题栏*/
.picc-inside-titleBar{
    height: 44px;
    background: #EDEEF0;
    border-radius: 4px 4px 0 0;
    font-size: 14px;
    color: #292B34;
    padding: 12px 16px;
    position: relative;
    .title-area{
        height: 20px;
        line-height: 20px;
    }
    .operation-area{
        height: 100%;
        line-height: 20px;
        padding: 12px 16px 12px 0;
        position: absolute;
        right: 0;
        top: 0;
    }
}
.picc-unfold-switch{
    position: relative;
    & > .unfold-switch--border{
        width: calc(100% - 32px);
        height: 1px;
        position: absolute;
        top: 0;
        left: 16px;
        border-top: 1px dotted rgba(41,43,52,0.3);
    }
    & > .unfold-switch--content{
        padding: 12px 2px;
        background: #f7f8fe;
    }
    & > .operation-btn{
        height: 36px;
        text-align: center;
        cursor: pointer;
        .btn-group{
            height: 20px;
            line-height: 20px;
            & > span{
                height: 20px;
                line-height: 20px;
                margin-left: 0px;
                display: inline-block;
                vertical-align: middle;
                float: right;
            }
            & > i{
                width: 20px;
                height: 20px;
                line-height: 20px;
                display: inline-block;
                vertical-align: middle;
                float: right;
            }
        }
    }
}
</style>
