<!--
    ===============
    二维码支付 组件
    ===============
    功能：    二维码支付
    实现方式： 1. 二维码支付通过modal层弹出；
              2. 因为支付涉及到好多和后端接口交互的功能，为了更加灵活，就没有把element ui
                 的Modal弹层组件放进来；
              3. @type 支付类型 微信支付 | 微信支付；
              4. @total 金额总额度
              5. @qrCode 支付二维码 TODO:此处可能会使用base64或者其他第三方支付生成的文件；
              6. @feeType 费用名称
              7. @countdown 该值为倒计时时间，因为逻辑比较复杂，不宜放在公共组件中渲染
                 TODO: 可能需要修改或者调整;
              8. @orderNo 订单号 TODO: 可能会改成slot后期会调整
 -->
<template id="">
    <div class="picc-qr-payment">
      <div class="picc-qr-payment-head">
        <h2>打开{{ type == 'alipay' ? '支付宝' : '微信'}}进行支付</h2>
        <b class="picc-qr-payment-fee">￥ {{ total }}</b>
        <b class="picc-qr-payment-fee-type">{{ feeType }}</b>
      </div>
      <div class="picc-qr-payment-img">
        <img :src="qrCode">
      </div>
      <div class="picc-qr-payment-foot">
        <h2 class="picc-qr-payment-expired-time">
          {{ countdown }}
        </h2>
        <b class="picc-qr-payment-expired-tit">二维码有效时间</b>
        <b class="picc-qr-payment-order-num"><span>订单号</span>{{ orderNo }}</b>
        <el-button type="primary" icon="el-icon-link" round plain @click="copy">复制链接</el-button>
      </div>
    </div>
</template>
<script>
export default {
    name: 'QrCodePay',
    props: {
        type: {
            type: String,
            required: true
        },
        total: {
            type: String,
            required: true
        },
        feeType: {
            type: String,
            default: function(){
                return '总保费(元)'
            }
        },
        qrCode: {
            type: String,
            required: true
        },
        countdown: {
            type: String,
            required: true
        },
        orderNo: {
            type: String,
            required: true
        }
    },
    data () {
        return {}
    },
    methods: {
        copy () {}
    }
}
</script>
