<!--
    ====================
    菜单 组件
    ====================
    TODO: 组件有待优化；
    功能：    对当前的登录的用户有权限的菜单进行展示，并实现点击切换页面的功能，实现二级菜单过长的自动换列；
    实现原因：当前用户对业务功能的需要和操作的便利；
    实现方式： 1. 通过vuex中的permission_routes进行数据数据的获取，通过对数据的循环展示渲染菜单栏
              2. 将也二级和三级菜单展示出来然后通过对当前页面的高度的计算和对二级菜单和三级菜单的累加进行
                  比较实现根据长度动态换列
              3. 鼠标滑过一级菜单时一级菜单展开滑过以及菜单按钮时代开二级菜单，点击二级菜单进行路由的跳转，
                  且要求二级菜单数据过长需要动态换列

 -->
<template id="">
  <div :class="isSpread ? 'picc-sidebar' : 'picc-sidebar is-shrink'">
    <div class="picc-sidebar-logo">
      <img :src="logo" alt="">
      <span>{{ title }}</span>
    </div>
    <div class="" v-for="(item, index) in permission_routes"  :key="index">
        <sidebar-item
          v-if="!item.hidden"
          :hidden="item.hidden"
          :path="item.path"
          :meta="item.meta"
          :route="item"
        />
    </div>

  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import SidebarItem from './SidebarItem.vue'
export default {
    name:'Sidebar',
    components: {
        SidebarItem
    },
    props: {
        title: {
            type: String,
            default: ''
        },
        isSpread: {
            type: Boolean,
            default: false
        },
        logo: {
            type: String
        }
    },

    computed: {
        ...mapGetters(['permission_routes']),
        isDashboard() {
            return !!this.isSpread
        }
    },
    created() {},
    mounted() {
    //
        this.$nextTick(() => {
            const sidebarStatus = document.querySelector('.el-aside')
            sidebarStatus.addEventListener('mouseover', () => {
                if (sidebarStatus.firstChild.classList.contains('is-shrink')) {
                    //
                    sidebarStatus.firstChild.classList.remove('is-shrink')
                }
            })
            sidebarStatus.addEventListener('mouseout', () => {
                if (!sidebarStatus.firstChild.classList.contains('is-shrink')) {
                    //
                    if (!this.isDashboard) {
                        sidebarStatus.firstChild.classList.add('is-shrink')
                    }
                }
            })
        })
    }
}
</script>
