<!--
    ===============
    结果页提示 组件
    ===============
    功能：    结果页提示
    实现方式： 1. 一共有三种状态 success | warning | error；
              2. 一共设置有两个slot，因为结果页主体部分和message的距离不一致，所以
                 采用slot来灵活搭配；
              3. 主体部分的slot名称为body, 底部按钮部分的slot名称为footer
              4. @type为结果页的类型 success | warning | error；
              5. @message为结果页图标下方要提示的内容区域；
 -->
<template >
    <div :class='"picc-result picc-result-" + type' style="margin:0 auto;">
      <div class="picc-result-box">
        <div>
          <i :class='"picc-icon picc-icon-" + type' />
        </div>
        <div class="picc-result-msg">
          {{ message }}
        </div>
        <div class="picc-result-body">
            <slot name="body" />
        </div>
      </div>
        <slot name="footer" />
    </div>
</template>
<script>
export default {
    name: 'Result',
    props: {
        type: {
            type: String,
            required: true,
            default: function() {
                return ''
            }
        },
        message: {
            type: String,
            default: function() {
                return ''
            }
        }

    }
}
</script>
