import CascadeSelector from '../src/components/CascadeSelector/CascadeSelector'
import Sidebar from '../src/components/Sidebar/index'
import SidebarItem from '../src/components/Sidebar/SidebarItem'
import Toolbar from '../src/components/Toolbar/index'
import ToolContainer from '../src/components/Toolbar/ToolContainer'
import TimePicker from '../src/components/TimePicker/index'
import BoxHead from '../src/components/BoxHead/index'
import FullScreenBar from '../src/components/FullScreenBar/index'
import FormCard from '../src/components/FormCard/index'
import TabItem from '../src/components/TabItem/index'
import Result from '../src/components/Result'
import QrCodePay from '../src/components/QRCodePay'
import BottomBar from '../src/components/BottomBar'
import AnchorPoint from '../src/components/AnchorPoint'
import Navbar from '../src/components/Navbar'

CascadeSelector.install = function(Vue) {
    Vue.component(CascadeSelector.name, CascadeSelector)
}

Navbar.install = function(Vue) {
    Vue.component(Navbar.name, Navbar)
}

AnchorPoint.install = function(Vue) {
    Vue.component(AnchorPoint.name, AnchorPoint)
}

SidebarItem.install = function(Vue) {
    Vue.component(SidebarItem.name, SidebarItem)
}

BottomBar.install = function(Vue) {
    Vue.component(BottomBar.name, BottomBar)
}

Result.install = function(Vue) {
    Vue.component(Result.name, Result)
}

QrCodePay.install = function(Vue) {
    Vue.component(QrCodePay.name, QrCodePay)
}

Sidebar.install = function(Vue) {
    Vue.component(Sidebar.name, Sidebar)
}

Toolbar.install = function(Vue) {
    Vue.component(Toolbar.name, Toolbar)
}

ToolContainer.install = function(Vue) {
    Vue.component(ToolContainer.name, ToolContainer)
}

TimePicker.install = function(Vue) {
    Vue.component(TimePicker.name, TimePicker)
}

BoxHead.install = function(Vue) {
    Vue.component(BoxHead.name, BoxHead)
}

FullScreenBar.install = function(Vue) {
    Vue.component(FullScreenBar.name, FullScreenBar)
}
FormCard.install = function(Vue) {
    Vue.component(FormCard.name, FormCard)
}
TabItem.install = function(Vue) {
    Vue.component(TabItem.name, TabItem)
}

const components = [
    CascadeSelector,
    Sidebar,
    Toolbar,
    ToolContainer,
    TimePicker,
    BoxHead,
    FullScreenBar,
    FormCard,
    TabItem,
    Result,
    QrCodePay,
    BottomBar,
    SidebarItem,
    AnchorPoint,
    Navbar
]
const install = function (Vue) {
    if (install.installed) return;
    install.installed = true;
    components.map(component => {
        Vue.component(component.name, component)
    })
}

if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue);
}
//
export {
    install,
    CascadeSelector,
    Sidebar,
    Toolbar,
    ToolContainer,
    TimePicker,
    BoxHead,
    FullScreenBar,
    FormCard,
    TabItem,
    Result,
    QrCodePay,
    BottomBar,
    SidebarItem,
    AnchorPoint,
    Navbar
};
